<template lang="pug">

	v-scroll.about(:params="{ number: mobile ? 3 : 2 }" @changeView="changeView")
		template(#0)
			.main
				h2.title Создание сайтов под ключ
				.text Здравствуйте, меня зовут Евгений, я занимаюсь созданием сайтов под ключ. На уровне эксперта владею вёрсткой и созднием комплексных и функциональных frontend приложений.
		
		template(v-if="mobile" #1)
			.techs
				.list.tech
					.list-title Технологии
					ul
						li html + css
							ul
								li bootstrap (при надобности)
						li js
							ul
								li Vue.js
								li Vanilla
								li jQuery (при надобности)
						li node.js + express
						li postgreSql
		template(v-if="mobile" #2)
			.techs
				.list.skills
					.list-title Навыки
					ul
						li Понятный и легко-поддерживаемый код (что сэкономит вам мнооого денег)
						li Анимированние всего и вся
						li Качественная адаптация под разные устройства

		template(v-else #1)
			.techs
				.list.tech
					.list-title Технологии
					ul
						li html + css
							ul
								li bootstrap (при надобности)
						li js
							ul
								li Vue.js
								li Vanilla
								li jQuery (при надобности)
						li node.js + express
						li postgreSql
				.list.skills
					.list-title Навыки
					ul
						li Понятный и легко-поддерживаемый код (что сэкономит вам мнооого денег)
						li Анимированние всего и вся
						li Качественная адаптация под разные устройства

		template(#next)
			span(v-if="!mobile || activeView == 0") Навыки и технологии
			span(v-if="mobile && activeView == 1") Навыки
			.arrow
		template(#back)
			span(v-if="!mobile || activeView == 1") Общая информация
			span(v-if="mobile && activeView == 2") Технологии
			.arrow

</template>

<script>
import Scroll from './scroll'

export default {
	mounted(){
		this.checkWindowWidth()
		window.addEventListener('resize', () => this.checkWindowWidth())
	},
	data(){
		return {
			mobile: false,
			activeView: 0
		}
	},
	methods: {
		changeView(index){
			this.activeView = index
		},
		checkWindowWidth(){
			if (this.mobile != window.innerWidth < 600)
				this.mobile = window.innerWidth < 600
		},
	},
	components: {
		'v-scroll': Scroll
	}
}

</script>

<style lang="stylus">

.about
	.view
		display flex
		align-items center
		justify-content center
	.nav
		.next, .back
			.arrow
				display block
				position absolute
				background-image url('/img/arrow-down.svg')
				background-size contain
				background-position center
				width 20px
				height 20px
				left 50%
				margin-left -10px
		.next
			.arrow
				top 100%
		.back
			.arrow
				top -20px
				transform rotate(180deg)


</style>

<style lang="stylus" scoped>

.about
	position relative
	z-index 2
	display flex
	align-items center
	justify-content center
	flex-direction column
	height 100%
	height 100vh
	width 1080px
	margin auto
	.title
		font-size 40px
		margin-bottom 20px
		width 600px
		text-align center
	.text
		font-size 21px
		line-height 1.4
		width 600px
		text-align center

.techs
	width 800px
	display flex
	align-items center
	justify-content space-between
	position relative
	.list
		width 370px
		&.skills
			text-align right
		&-title
			font-size 30px
			margin-bottom 10px
		ul
			list-style-type circle
		li
			font-size 25px
			padding 8px 0
			ul
				padding-left 25px
				li
					padding 0

@media (max-width: 1080px)
	.about
		width 100%
		.title
			font-size 40px
	.techs
		width 600px
		.list
			width 270px
			li
				font-size 20px

@media (max-width: 1080px)
	.about
		.title
			width 270px
			font-size 30px
		.text
			width 270px
			font-size 18px
	.techs
		width 300px
		flex-direction column
		.list
			width 270px
			&-title
				text-align center
			&.skills
				text-align left
			li
				font-size 20px

</style>